<template>
  <div id="addchild">
    <div v-if="notNull(data.Question)" class="mb-5" align="right">
      <div style="max-width: 90%" class="grey lighten-4 pa-2 rounded">
        <div class="mb-2 text-caption font-weight-bold" align="left">
          Child of Option {{ i + 1 }}.
          <span class="success--text">[Radio, TextBox]</span>
        </div>
        <draggable
          element="span"
          v-model="data.Question"
          v-bind="dragOptions"
          :group="{ name: 'items' }"
          ghost-class="ghost"
          :scroll-sensitivity="250"
          @start="dragging = true"
          @end="dragging = false"
          @sort="
            getQID();
            justOnly(data.Question);
          "
          handle=".move"
          :sort="true"
        >
          <transition-group type="transition" tag="v-list">
            <div class="mb-2" v-for="(element, j) in data.Question" :key="j">
              <div class="px-3">
                <v-row>
                  <v-col cols="10">
                    <component
                      align="left"
                      :is="element.Type"
                      :data="element"
                      :getQID="getQID"
                      :key="j"
                    ></component>
                  </v-col>
                  <v-col class="move">
                    <v-btn
                      text
                      icon
                      color="gray"
                      v-on:click="
                        data.Question.splice(j, 1);
                        getQID();
                      "
                    >
                      <v-icon>mdi-close</v-icon>
                    </v-btn>
                    <v-btn class="mt-2" text icon color="gray">
                      <v-icon>mdi-drag</v-icon>
                    </v-btn>
                  </v-col>
                </v-row>
              </div>
            </div>
          </transition-group>
        </draggable>
      </div>
    </div>
  </div>
</template>

<script>
import draggable from "vuedraggable";

export default {
  name: "AddChild",
  props: {
    i: Number,
    data: {
      Question: Array,
    },
    getQID: Function,
  },
  data() {
    return {};
  },
  methods: {
    justOnly(questions) {
      questions.map((val, i) => {
        if (["TextBox", "Radio"].includes(val.Type)) {
          return;
        } else {
          questions.splice(i, 1);
        }
      });
    },
    notNull(questions){
      return questions != undefined;
    }
  },
  components: {
    TextBox: () => import('../TextBox'),
    Radio: () => import('../Radio'),
    draggable,
    
  },
  computed: {
    dragOptions() {
      return {
        animation: 200,
        disabled: false,
        ghostClass: "ghost",
      };
    },
  },
};
</script>

