<template>
  <div id="Session">
    <div v-if="editable">
      <v-text-field
        v-model="data.Name"
        v-on:keyup.enter="editable = !editable"
        dense
        autofocus
      ></v-text-field>
      <v-text-field
        v-model="data.Des"
        v-on:keyup.enter="editable = !editable"
        dense
      ></v-text-field>
      <v-btn color="primary" @click="editable = !editable" block>
        <v-icon>mdi-check</v-icon>
      </v-btn>
    </div>
    <div v-if="!editable" v-on:click="editable = !editable">
      <v-tooltip right>
        <template v-slot:activator="{ on, attrs }">
          <div v-bind="attrs" v-on="on" rounded>
            <div
              align="center"
              class="blue lighten-4 pa-2 mb-2 font-weight-bold rounded blue--text text--darken-4"
              v-text="data.Name"
            ></div>
            <div v-text="data.Des"></div>
          </div>
        </template>
        <span
          >Nhấn để chỉnh sửa <br />
          tiêu đề của phần</span
        >
      </v-tooltip>
    </div>
    <draggable
      element="span"
      v-model="data.Question"
      v-bind="dragOptions"
      :group="{ name: 'items' }"
      :scroll-sensitivity="250"
      @start="dragging = true"
      @end="dragging = false"
      @sort="handleSort();"
      handle=".move"
      :sort="true"
    >
      <transition-group type="transition" tag="v-list">
        <v-hover
          open-delay="0"
          v-slot:default="{ hover }"
          v-for="(element, i) in data.Question"
          :key="i"
        >
          <v-card
            :ripple="false"
            flat
            outlined
            class="mb-3 hold"
            :class="{ 'on-hover': hover }"
          >
            <div class="py-0 move" dense flat>
              <v-row style="height: 30px">
                <v-col class="drag text-center" cols="2">
                </v-col>
                <v-col
                  style="height: 30px"
                  class="d-flex justify-space-around drag"
                >
                  <v-icon v-if="hover">mdi-drag-horizontal</v-icon>
                </v-col>
                <v-col cols="2" class="d-flex justify-end">
                  <v-btn
                    v-if="hover"
                    class="mt-2"
                    text
                    icon
                    color="gray"
                    v-on:click="
                      data.Question.splice(i, 1);
                      handleSort();
                    "
                  >
                    <v-icon>mdi-close</v-icon>
                  </v-btn>
                </v-col>
              </v-row>
            </div>
            <div class="px-3">
              <div v-bind="attrs" v-on="on" rounded>
                <component
                  :is="element.Type"
                  :data="element"
                  :getQID="handleSort"
                  :key="i"
                ></component>
              </div>
              <div style="height: 15px;" class="mb-2">
                <transition name="slide-fade">
                  <div class="text-caption text-end font-weight-normal green--text text--darken-2" v-if="(!dragging?hover:false)" v-text="'Nhấn vào câu hỏi để sửa'"></div>
                </transition>
              </div>
            </div>
            <!-- <div class="text-end px-2">
              
            </div> -->
          </v-card>
        </v-hover>
      </transition-group>
    </draggable>
  </div>
</template>

<script>
import Radio from "./Radio.vue";
import Checkbox from "./CheckBox";
import TextBox from "./TextBox.vue";
import TextArea from "./TextArea.vue";
import MarkTable from "./MarkTable.vue";
import FileInput from "./FileInput.vue";
import draggable from "vuedraggable";

export default {
  name: "Session",
  props: {
    handleSort: Function,
    data: {
      QID: String,
      Name: String,
      Des: String,
      Question: Array,
      dragging: false,
    },
  },
  data: function () {
    return {
      items: {
        Radio: "Trắc nghiệm",
        Checkbox: "Hộp kiểm",
        TextBox: "Trả lời ngắn",
        TextArea: "Đoạn",
        MarkTable: "Marktable",
      },
      editable: false,
    };
  },
  methods: {},
  computed: {
    dragOptions() {
      return {
        animation: 200,
        disabled: false,
        ghostClass: "ghost",
        chosenClass: "ghost",
      };
    },
  },
  components: {
    draggable,
    Checkbox,
    TextBox,
    Radio,
    FileInput,
    MarkTable,
    TextArea,
  },
};
</script>
<style scoped>
.flip-list-move {
  transition: transform 0.5s;
}
.no-move {
  transition: transform 0s;
}
.list-group {
  min-height: 20px;
}
.list-group-item {
  cursor: move;
}
.list-group-item i {
  cursor: pointer;
}
/* .hold{
  background-color: rgb(202, 202, 202)!important;
} */
/* .hold:hover {
  outline: 2px solid rgba(128, 128, 128, 0.425);
} */
.drag {
  cursor: grab;
}
/* .ghost {
  opacity: 0.8;
  background: rgba(0, 128, 179, 0.274);
} */

.slide-fade-enter-active {
  transition: all .3s ease;
}
.slide-fade-leave-active {
  transition: all .3s cubic-bezier(1.0, 0.5, 0.8, 1.0);
}
.slide-fade-enter, .slide-fade-leave-to
/* .slide-fade-leave-active below version 2.1.8 */ {
  transform: translateX(10px);
  opacity: 0;
}
</style>