<template>
  <div id="checkbox">
    <div v-if="editable">
      <v-text-field
        label="Tiêu đề câu hỏi"
        class="my-5"
        v-model="data.Text"
        v-on:keyup.enter="editable = !editable"
        dense
        @focus="focusable"
      ></v-text-field>
      <div v-for="(item,i) in data.Option" :key="i">
        <v-text-field
            :label="'Option ' + (i+1)"
            class="my-2"
            v-model="data.Option[i].Text"
            v-on:keyup.enter="editable = !editable"
            dense
            @focus="focusable"
            >
            <template slot="append-outer">
              <v-btn @click="addChild(i); getQID(); added = !added" color="success" text :key="i">Child</v-btn>
              <v-icon @click="data.Option.splice(i,1); editable = -1">mdi-delete</v-icon>
            </template>
        </v-text-field>
        <AddChild :i="i" :data="item" :getQID="getQID" ></AddChild>
    </div>
      <v-btn
        class="my-2"
        color="blue"
        v-on:click="
          data.Option.push({ 
            QID: '',
            Text: 'Checkbox ' + (data.Option.length + 1) }); getQID()"
        outlined
        block
        >Thêm</v-btn
      >
      <v-btn color="primary" class="mb-3" @click="editable = !editable" block>
        <v-icon>mdi-check</v-icon>
      </v-btn>
    </div>

    <div v-if="!editable" @click="editable = !editable">
      <div class="my-3 font-weight-bold">{{ data.Text }}</div>
      <draggable
        v-model="data.Option"
        element="span"
        @sort="getQID()"
        :group="{ name: 'question' }"
        :sort="true"
        handle=".handle"
        v-bind="dragOptions"
      >
        <transition-group type="transition" class="py-0" group tag="div">
          <div v-for="(item,i) in data.Option" :key="i">
            <v-checkbox class="handle my-0 py-0" :label="item.Text" />
          </div>
        </transition-group>
      </draggable>
    </div>
  </div>
</template>

<script>
import draggable from "vuedraggable";
import AddChild from "./child/AddChild";

export default {
  name: "CheckBox",
  props: {
    getQID: Function,
    data: {
      GroupID: String,
      Text: String,
      Option: Array,
    },
  },
  data: function () {
    return {
      editable: false,
      added: false,
    };
  },
  methods: {
    focusable(e) {
      e.target.select();
    },
    addChild(index) {
      this.data.Option = this.data.Option.map((option, idx) => {
        if (index === idx) {
          return {
            ...option,
            Des: "",
            Question: [
              // {
              //   Type: "TextBox",
              //   Text: "Tiêu đề",
              //   QID: '',
              //   Holder: "Mô tả",
              // },
            ],
          };
        }
        return option;
      });
    }
  },
  components: {
    draggable,
    AddChild
  },
  computed: {
    dragOptions() {
      return {
        animation: 200,
        disabled: false,
        ghostClass: "ghost",
      };
    },
  },
};
</script>