<template>
  <div id="jsonviewer">
    <v-dialog scrollable v-model="dialog" width="600px">
      <template v-slot:activator="{ on, attrs }">
        <v-btn fab small dark v-bind="attrs" v-on="on" text>
          <v-icon>mdi-eye</v-icon>
        </v-btn>
      </template>
      <v-card v-click-outside="onClickOutside">
        <v-toolbar elevation="3">
          <v-spacer></v-spacer>
          <v-btn :color="(isCopy)?'success':'grey darken-1'" class="mr-3" text @click="copyJson">
            {{ (isCopy)?'':'copy' }} <v-icon v-if="isCopy">mdi-check</v-icon>
          </v-btn>
          <v-btn
            color="grey darken-1"
            class="mr-3"
            text
            @click="export2txt"
          >
            Tải xuống
          </v-btn>
          <v-btn
            color="error"
            class="mr-3"
            text
            @click="dialog = false; isCopy = false"
          >
            đóng
          </v-btn>
        </v-toolbar>
        <v-card-text>
          <pre class="mt-3">{{ listString }}</pre>
        </v-card-text>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
export default {
  name: "jsonviewer",
  props: {
    data: Object,
    nameTemplates: String
  },
  data: function () {
    return {
      dialog: false,
      isCopy: false
    };
  },
  methods: {
    export2txt() {
      const originalData = this.data;
      const a = document.createElement("a");
      a.href = URL.createObjectURL(
        new Blob([JSON.stringify(originalData, null, 2)], {
          type: "application/json",
        })
      );
      a.setAttribute("download", this.nameTemplates + ".json");
      document.body.appendChild(a);
      a.click();
      document.body.removeChild(a);
    },
    onClickOutside () {
        this.isCopy = false
    },
    copyJson() {
      navigator.clipboard.writeText(JSON.stringify(this.data, null, 2));
      this.isCopy = true;
    },
  },
  computed: {
    listString() {
      return JSON.stringify(this.data, null, 2);
    },
  },
};
</script>

<style>
pre {
  overflow-x: auto;
  white-space: pre-wrap;
  white-space: -moz-pre-wrap;
  white-space: -pre-wrap;
  white-space: -o-pre-wrap;
  word-wrap: break-word;
}
</style>