<template>
  <div id="app">
    <v-app>
      <v-main class="grey lighten-2">
        <v-row class="my-1" justify="center" no-gutters>
          <div class="float">
            <v-card rounded="2" elevation="5" class="mr-2">
              <bottombar :data="templates" :scroll="scrollToBottom"></bottombar>
            </v-card>
          </div>
          <v-col style="max-width: 640px !important" tile>
            <v-app-bar app elevation="0" class="blue darken-4" dense fixed>
              <v-toolbar flat dense color="blue darken-4" dark>
                <v-tooltip bottom>
                  <template v-slot:activator="{ on, attrs }">
                    <v-text-field
                      v-bind="attrs"
                      v-on="on"
                      background-color="#00000000"
                      v-model="nameTemplates"
                      loading="false"
                      dense
                      flat
                      solo
                      hide-details
                      single-line
                      placeholder="Tên mẫu"
                    />
                  </template>
                  Tên mẫu
                </v-tooltip>
                <v-spacer></v-spacer>
                <v-tooltip bottom>
                  <template v-slot:activator="{ on, attrs }">
                    <div v-bind="attrs" v-on="on" rounded>
                      <precommit
                        class="mr-3"
                        :nameTemplates="nameTemplates"
                        :data="templates"
                      ></precommit>
                    </div>
                  </template>
                  <span>Tải lên</span>
                </v-tooltip>
                <v-tooltip bottom>
                  <template v-slot:activator="{ on, attrs }">
                    <div v-bind="attrs" v-on="on" rounded>
                      <jsonviewer
                        class="mr-3"
                        :nameTemplates="nameTemplates"
                        :data="templates"
                      ></jsonviewer>
                    </div>
                  </template>
                  <span>Xem json</span>
                </v-tooltip>
                <v-tooltip bottom>
                  <template v-slot:activator="{ on, attrs }">
                    <div v-bind="attrs" v-on="on" rounded>
                      <v-file-input
                        style="max-width: 30px"
                        accept="application/json"
                        prepend-icon="mdi-import"
                        hide-input
                        @change="importTemplates"
                      >
                      </v-file-input>
                    </div>
                  </template>
                  <span>Nhập json</span>
                </v-tooltip>
              </v-toolbar>
            </v-app-bar>
            <v-card
              elevation="0"
              class="py-2 mt-1 transparent"
              style="margin-bottom: 150px"
            >
              <div class="mx-0">
                <v-tooltip right>
                  <template v-slot:activator="{ on, attrs }">
                    <div v-bind="attrs" v-on="on" rounded>
                      <formtitle :data="templates"></formtitle>
                    </div>
                  </template>
                  <span>Nhấn để chỉnh sửa</span>
                </v-tooltip>

                <draggable
                  v-model="templates.Part"
                  element="span"
                  v-bind="dragOptions"
                  @sort="handleSort"
                  :group="{ name: 'part' }"
                  :sort="true"
                  handle=".dragbar"
                >
                  <v-card
                    class="px-3 mt-2"
                    flat
                    outlined
                    v-for="(element, i) in templates.Part"
                    :key="i"
                  >
                    <v-toolbar dense flat tile class="dragbar" align="right">
                      <v-row>
                        <v-col cols="2"></v-col>
                        <v-col class="d-flex justify-space-around"> </v-col>
                        <v-col cols="2" class="d-flex justify-end">
                          <div>
                            <v-menu bottom right>
                              <template v-slot:activator="{ on, attrs }">
                                <v-btn icon v-bind="attrs" v-on="on">
                                  <v-icon>mdi-dots-vertical</v-icon>
                                </v-btn>
                              </template>

                              <v-list class="pa-0">
                                <v-list-item>
                                  <v-btn
                                    :ripple="false"
                                    class="ma-0"
                                    text
                                    color="red"
                                    v-on:click="templates.Part.splice(i, 1)"
                                    v-text="items[0]"
                                  ></v-btn>
                                </v-list-item>
                              </v-list>
                            </v-menu>
                          </div>
                        </v-col>
                      </v-row>
                    </v-toolbar>
                    <session
                      :data="element"
                      :handleSort="handleSort"
                      :key="i"
                    ></session>
                  </v-card>
                  <v-card elevation="0" class="mt-1 pa-1 rounded text-end">
                    <div>
                      <v-btn
                        class="blue darken-2 py-4 rounded white--text"
                        v-on:click="
                          templates.Part.push({
                            Name: 'THÔNG TIN',
                            Des: 'Mô tả',
                            Question: [],
                          });
                          handleSort();
                          scrollToBottom();
                        "
                        :ripple="false"
                        small
                        tile
                        >+ thêm phần</v-btn
                      >
                    </div>
                  </v-card>
                </draggable>
              </div>
              <!-- <v-toolbar
                outlined
                elevation="5"
                class="text-end sticky-container"
                flat
                dense
                color="white"
                dark
                v-sticky
                sticky-offset="offset"
                sticky-side="bottom"
              >
                <v-spacer></v-spacer>
                <div>
                  <v-btn
                    class="blue--text text--darken-4 rounded"
                    v-on:click="
                      templates.Part.push({
                        Name: 'THÔNG TIN',
                        Des: 'Mô tả',
                        Question: [],
                      });
                      handleSort();
                      scrollToBottom();
                    "
                    :ripple="false"
                    small
                    tile
                    outlined
                    >+ thêm phần</v-btn
                  >
                </div>  
              </v-toolbar> -->

              <!-- <v-toolbar
                outlined
                elevation="5"
                class="my-2 text-end sticky-container"
                flat
                dense
                color="white"
                dark
                v-sticky
                sticky-offset="offset"
                sticky-side="bottom"
              >
                <div>
                  <bottombar
                    :data="templates"
                    :scroll="scrollToBottom"
                  ></bottombar>
                </div>
                <v-spacer></v-spacer>
                <div>
                  <v-btn
                    class="blue--text text--darken-4 rounded"
                    v-on:click="
                      templates.Part.push({
                        Name: 'THÔNG TIN',
                        Des: 'Mô tả',
                        Question: [],
                      });
                      handleSort();
                      scrollToBottom();
                    "
                    :ripple="false"
                    small
                    tile
                    outlined
                    >+ thêm phần</v-btn
                  >
                </div>
              </v-toolbar> -->
            </v-card>
            <div style="height: 100px"></div>
            <div id="endpoint" style="max-height: 1px"></div>
          </v-col>
        </v-row>
      </v-main>
    </v-app>
  </div>
</template>

<script>
import session from "./components/Session";
import formtitle from "./components/Formtitle";
import jsonviewer from "./components/dialog/JsonViewer";
import precommit from "./components/dialog/PreCommit";
import draggable from "vuedraggable";
import bottombar from "./components/layout/bottom-navigation";
import Sticky from "vue-sticky-directive";

export default {
  directives: {
    Sticky,
  },
  name: "App",

  data: function () {
    return {
      items: ["Xóa phần"],
      snackbar: {
        status: false,
        snackbar: false,
        text: "",
        timeout: 2000,
      },
      nameTemplates: "qaform",

      templates: {
        templateID: "",
        logo_org:
          "https://lhu.edu.vn/ViewPage/LHUVN/_default/image/truong_dai_hoc_lac_hong_logo.png",
        name_org: "Trường Đại học Lạc Hồng",
        url_finishRedirect: "https://lhu.edu.vn",
        Title: "TÊN PHIẾU KHẢO SÁT",
        Des: "Mô tả của phiếu khảo sát",
        url_login: "",
        url_template: "",
        Part: [
          {
            Name: "THÔNG TIN CHUNG",
            Des: "",
            Question: [],
          },
        ],
      },
      loadingBtn: false,
    };
  },
  methods: {
    scrollToBottom() {
      let element = document.getElementById("endpoint");
      element.scrollIntoView({ behavior: "smooth", block: "end" });
    },
    handleSort() {
      this.templates.Part.map((value, index) => {
        const partID = `P${++index}`;
        value.Question.map((e, i) => {
          const key = `${partID}-${(++i).toString().padStart(2, "0")}`;
          if (e.Type === "MarkTable") {
            e.Topic.map((val, j) => {
              val.GroupID = `${key}-T${++j}`;
              val.Question.map((ques, k) => {
                ques.QID = `${val.GroupID}-${(++k)
                  .toString()
                  .padStart(2, "0")}`;
              });
            });
            return;
          }
          if (e.Type === "Radio") {
            e.QID = key;
            e.Option.map((val) => {
              if (val.Question != undefined) {
                val.Question.map((ques, k) => {
                  ques.QID = `${e.QID}-${(++k).toString().padStart(2, "0")}`;
                });
              }
            });
            return;
          }
          if (e.Type === "Checkbox") {
            e.GroupID = key;
            e.Option.map((val, j) => {
              val.QID = `${e.GroupID}-${(++j).toString().padStart(2, "0")}`;
              if (val.Question != undefined) {
                val.Question.map((ques, k) => {
                  ques.QID = `${val.QID}-${(++k).toString().padStart(2, "0")}`;
                });
              }
            });
            return;
          }
          e.QID = key;
        });
      });
    },
    importTemplates(files) {
      if (files.length <= 0) {
        return false;
      }
      const fr = new FileReader();
      fr.onload = (e) => {
        const result = JSON.parse(e.target.result);
        this.templates = JSON.parse(JSON.stringify(result, null, 2));
      };
      fr.readAsText(files);
    },
    uuid(e) {
      if (e.uid) return e.uid;
      const key = Math.random().toString(16).slice(2);
      this.$set(e, "uid", key);
      return e.uid;
    },
    generateUUID() {
      const key = Math.floor(Math.random() * (1000000 - 1)) + 100000;
      return key;
    },
    handleClone(item) {
      // Create a fresh copy of item
      let cloneMe = JSON.parse(JSON.stringify(item));
      this.$delete(cloneMe, "uid");
      return cloneMe;
    },
  },
  updated() {
    this.scrollToBottom();
  },
  mounted() {
    this.scrollToBottom();
  },
  components: {
    draggable,
    session,
    formtitle,
    jsonviewer,
    precommit,
    bottombar,
  },
  computed: {
    dragOptions() {
      return {
        animation: 100,
        disabled: false,
        ghostClass: "ghost",
      };
    },
    listString() {
      return JSON.stringify(this.templates, null, 2);
    },
  },
};
</script>

<style>
::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #f1f1f1;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #0d47a1;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #05357c;
}

.grab {
  cursor: -webkit-grab;
  cursor: grab;
}

/* .sticky {
  position: -webkit-sticky;
  position: sticky;
  top: 30%;
} */

.float {
  position: fixed;
  top: calc(100% / 2 - 160px);
  left: calc(100% / 2 - 490px);
  text-align: center;
  z-index: 1;
}
@media only screen and (max-width: 1024px) {
  .float {
    left: calc(100% / 2 - 390px);
  }
}
</style>

<style lang="scss">
@import url("https://fonts.googleapis.com/css? family=Oxygen:300,400,700&display=swap");
@import url("https://fonts.googleapis.com/css? family=Comfortaa&display=swap");

$body-font-family: "Arial";
$title-font: "Arial";

.v-application {
  font-family: $body-font-family, sans-serif !important;
  .title {
    // To pin point specific classes of some components
    font-family: $title-font, sans-serif !important;
  }
}
</style>>