<template>
  <div id="marktable">
    <div>
      <div v-if="editable" class="grey lighten-4 mt-2 px-1 py-3 rounded">
        <v-text-field
          label="Chỉnh sửa tiêu đề"
          class="my-2"
          v-model="data.Text"
          v-on:keyup.enter="editable = !editable"
          dense
          autofocus
          @focus="focusable"
        ></v-text-field>
        <v-textarea
          rows="2"
          auto-grow
          label="Chỉnh sửa mô tả"
          class="my-2"
          v-model="data.Des"
          v-on:keyup.enter="editable = !editable"
          dense
        />
        <div class="mb-2 caption font-weight-medium primary--text">
          Chỉnh sửa nội dung mức điểm
        </div>
        <div>
          <v-select
          v-model="scoreVal"
          class="my-0"
          style="max-width: 25%"
          :items="[1, 2, 3, 4, 5, 6]"
          :label="scoreVal.toString()"
          @change="handleScoreColumn"
          solo
          outlined
          flat
          dense
        ></v-select>
        </div>
        <v-row no-gutters>
          <div>{{updateRate}}</div>
          <v-col v-for="(item, i) in data.ValueArray" :key="i">
            <div
              class="py-2 text-center primary white--text font-weight-bold"
              outlined
            >
              {{ item.Value }}
            </div>
            <div class="px-2 grey lighten-2">
              <v-textarea
                rows="2"
                auto-grow
                v-on:keyup.enter="editable = !editable"
                v-model="item.Text"
                required
                @focus="focusable"
              ></v-textarea>
            </div>
          </v-col>
        </v-row>
        <v-btn class="my-3" color="primary" @click="editable = !editable" block>
          <v-icon>mdi-check</v-icon>
        </v-btn>
      </div>
      <div @click="editable = !editable" v-if="!editable">
        <div class="my-3 font-weight-bold" v-text="data.Text"></div>
        <div class="my-2" v-text="data.Des" />
      </div>
      <draggable
        v-model="data.Topic"
        element="span"
        :group="{ name: 'topics' }"
        :sort="true"
        @sort="getQID"
        v-bind="dragOptions"
        handle=".movebar"
      >
        <transition-group class="py-0" group tag="div">
          <v-card
            flat
            outlined
            tile
            class="hold mb-4 py-3 px-3"
            align="left"
            v-for="(item, i) in data.Topic"
            :key="i"
          >
            <v-toolbar class="movebar grey lighten-2" dense flat>
              <v-row>
                <v-col class="drag" cols="2"></v-col>
                <v-col class="d-flex justify-space-around drag"> </v-col>
                <v-col cols="2" class="d-flex justify-end">
                  <v-btn
                    class="mt-2"
                    text
                    icon
                    color="gray"
                    v-on:click="data.Topic.splice(i, 1);getQID()"
                  >
                    <v-icon>mdi-delete</v-icon>
                  </v-btn>
                </v-col>
              </v-row>
            </v-toolbar>
            <div v-if="edititem == item">
              <v-text-field
                label="Tiêu đề câu hỏi"
                class="my-2"
                v-model="item.Name"
                v-on:keyup.enter="edititem = !item"
                @focus="focusable"
                autofocus
              />
              <div v-for="(ques, j) in item.Question" :key="j">
                <v-text-field
                  class="my-2"
                  v-model="ques.Text"
                  v-on:keyup.enter="edititem = !item"
                  dense
                >
                  <template slot="append-outer">
                    <v-icon @click="item.Question.splice(j, 1);getQID()"
                      >mdi-delete</v-icon
                    >
                  </template>
                </v-text-field>
              </div>
              <v-btn
                class="mb-3"
                color="primary"
                outlined
                @click="
                  item.Question.push({
                    QID: '',
                    Text: item.Question.length + 1 + '. ',
                  });getQID()
                "
                block
              >
                <v-icon>mdi-plus</v-icon>
              </v-btn>
              <v-btn class="mb-3" color="primary" @click="edititem = {}" block>
                <v-icon>mdi-check</v-icon>
              </v-btn>
            </div>
            <div v-if="edititem != item" @click="edititem = item" class="mt-5">
              <div class="font-weight-bold green--text">{{ item.Name }}</div>
              <draggable
                v-model="item.Question"
                element="span"
                @sort="getQID"
                :group="{ name: 'question' }"
                :sort="true"
                handle=".handle"
                v-bind="dragOptions"
              >
                <transition-group
                  type="transition"
                  class="py-0"
                  group
                  tag="div"
                >
                  <div
                    class="handle"
                    v-for="(ques, j) in item.Question"
                    :key="j"
                  >
                    <div class="font-weight-bold">{{ ques.Text }}</div>
                    <!-- <v-rating class="my-3">
                        <template v-slot:item="props">
                          <v-btn
                            :color="props.isFilled ? 'primary' : 'grey lighten-1'"
                            medium
                            outlined
                            tile
                            :style="'height: 50px; width: 40px;' + (props.index > 0 ? ' border-left: none' : '')"
                            @click="props.click"
                          >{{props.index + 1}}</v-btn>
                        </template>
                      </v-rating> -->
                  </div>
                </transition-group>
              </draggable>
            </div>
          </v-card>
        </transition-group>
      </draggable>
      <v-btn
        class="my-2"
        color="blue"
        v-on:click="
          data.Topic.push({
            GroupID: '',
            Name: 'TIÊU ĐỀ CÂU HỎI',
            Question: [
              {
                QID: '',
                Text: '1. Câu hỏi',
              },
            ],
          });getQID()
        "
        outlined
        block
        >Thêm câu hỏi</v-btn
      >
    </div>
  </div>
</template>

<script>
import draggable from "vuedraggable";

export default {
  name: "marktable",
  props: {
    getQID: Function,
    data: {
      Text: String,
      Des: String,
      ValueArray: Array,
      Topic: Array,
    },
  },
  data: function () {
    return {
      scoreVal: 5,
      editable: false,
      edititem: {},
    };
  },
  methods: {
    handleScoreColumn(){
      let ori = this.data.ValueArray.length;
      const score = this.scoreVal;
      console.log(ori, score)
      if(ori >= score){
        this.data.ValueArray.splice(score,(ori-score))
        return;
      }
      while(ori<score){
        this.data.ValueArray.push({
          Value: this.data.ValueArray.length+1,
          Text: "Text"
        })
        ori = this.data.ValueArray.length;
      }

    },
    focusable(e) {
      e.target.select();
    },
  },
  components: {
    draggable,
  },
  computed: {
    updateRate(){
      // vm.data.Des = `${this.data.Des.split("<br/>")[0]}<br/>1. ${this.data.ValueArray[0].Text} <br/>2. ${this.data.ValueArray[1].Text} <br/>3. ${this.data.ValueArray[2].Text} <br/>4. ${this.data.ValueArray[3].Text}<br/>5. ${this.data.ValueArray[4].Text}`
      return '';
    },
    dragOptions() {
      return {
        animation: 200,
        disabled: false,
        ghostClass: "ghost",
      };
    },
  },
};
</script>
<style scoped>
.square {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 80px;
  width: 50px;
}

.hold:hover {
  /* border: 2px solid rgba(128, 128, 128, 0.425); */
  outline: 2px solid rgb(0, 162, 255);
}
</style>