<template>
  <div id="formtitle">
    <v-card outlined class="px-3">
      <div class="my-2" v-if="editable">
      <v-img :src="data.logo_org" height="92" contain></v-img>
      <v-text-field label="templateID" v-model="data.templateID" v-on:keyup.enter="switchVal" dense></v-text-field>
      <v-text-field label="url_login" v-model="data.url_login" v-on:keyup.enter="switchVal" dense></v-text-field>
      <v-text-field label="url_template" v-model="data.url_template" v-on:keyup.enter="switchVal" dense></v-text-field>
      <v-text-field label="url_finishRedirect" v-model="data.url_finishRedirect" v-on:keyup.enter="switchVal" dense></v-text-field>
      <v-text-field
        label="Link Logo"
        v-model="data.logo_org"
        v-on:keyup.enter="switchVal"
        dense
        autofocus
      ></v-text-field>
      <v-text-field label="name_org" v-model="data.name_org" v-on:keyup.enter="switchVal" dense></v-text-field>
      <v-text-field label="Title" v-model="data.Title" v-on:keyup.enter="switchVal" dense></v-text-field>
      <v-textarea label="Des" v-model="data.Des" v-on:keyup.enter="switchVal" dense></v-textarea>
      <v-btn color="primary" @click="switchVal" block><v-icon>mdi-check</v-icon></v-btn>
    </div>
    <div class="my-2" v-if="!editable" v-on:click="switchVal">
      <v-img :src="data.logo_org" height="92" contain></v-img>
      <div
        class="text-uppercase my-2 blue--text text--darken-3 font-weight-bold"
        style="font-size: 28.9px"
        v-text="data.name_org"
        align="center"
      ></div>
      <div
        class="text-uppercase my-2 font-weight-bold"
        style="color: #17a2b8!important; font-size:24px"
        v-text="data.Title"
        align="center"
      ></div>
      <div class="my-2" v-text="data.Des"></div>
    </div>
    </v-card>
  </div>
</template>

<script>
export default {
  name: "formtitle",
  props: {
    data: {
      name_org: String,
      Title: String,
      Des: String,
    },
  },
  
  data: function () {
    return {
      editable: false,
    };
  },
  methods: {
    switchVal() {
      this.editable = !this.editable;
    },
  },
};
</script>