<template>
  <div id="precommit">
    <v-dialog scrollable v-model="dialog" width="400px">
      <template v-slot:activator="{ on, attrs }">
        <v-btn fab small v-bind="attrs" v-on="on" icon @click="isTemplateIDNotNULL">
          <v-icon>mdi-upload</v-icon>
        </v-btn>
      </template>
      <v-card>
        <!-- <v-toolbar dense elevation="3">
          <v-spacer></v-spacer>
          <div class="grey--text">{{message}}</div>
        </v-toolbar> -->
        <div>
          <div class="ma-3" align="center">
            <v-date-picker
              width="100%"
              locale="vi-VN"
              color="blue darken-4"
              header-color="blue darken-4"
              v-model="dates"
              :show-current="false"
              range
            ></v-date-picker>
          </div>
          <div class="mx-3 mb-2">
            Mẫu sẽ hiện hành từ <b>{{ moment(dates[0]).format("DD/MM/YYYY") }}</b> đến <b>{{ moment(dates[1]).format("DD/MM/YYYY") }}</b>
          </div>
        </div>
        <div :class="`py-2 text-caption px-3 white--text ${(canCommit)?'success':'error'}`">{{ message }}</div>
        <v-toolbar elevation="3">
          <v-spacer></v-spacer>
          <v-btn v-if="isCommited" color="purple" class="mr-3" text @click="viewForm">Xem</v-btn>
          <v-btn :disabled="!canCommit" :color="(canCommit)?'success':'grey darken-1'" class="mr-3" text @click="requestTemplates">COMMIT</v-btn
          >
          <v-btn color="red" class="mr-3" text @click="dialog = false">
            đóng
          </v-btn>
        </v-toolbar>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import axios from "axios";
import moment from "moment";
export default {
  name: "precommit",
  props: {
    data: Object,
    nameTemplates: String,
    snackbar: Object,
  },
  data: function () {
    return {
      dates: [moment().format("YYYY-MM-DD"),moment().add(10, 'days').format("YYYY-MM-DD")],
      datesCommit: [],
      message: "Có thể tải lên",
      dialog: false,
      canCommit: false,
      isCommited: false,
      moment,
    };
  },
  methods: {
    viewForm: function () {   
      window.open("https://qa.lhu.edu.vn/" + this.data.templateID + "/1", "_blank");    
    },
    isTemplateIDNotNULL(){
        if (this.data.templateID === "") {
            this.message = "templateID rỗng !";
            this.canCommit = false;
            this.isCommited = false;
            return;
        }
        this.message = "Có thể tải lên";
        this.canCommit = true;
    },
    requestTemplates() {
      const token =
        "eyJ1aWQiOiJOVjAwMDEwNzgiLCJzaWQiOiI2OURGNDM3ODJCQkY5REMxIiwiZXhwIjoyMDc1ODEwOTY0fQ.1wJLPmPjhagKAf47-3tJLyCF1Bx1CnTL86YA_BlkG2A";
      const instance = axios.create({
        baseURL: "https://tapi.lhu.edu.vn/qa",
      });
      instance.defaults.headers.common["Authorization"] = token;
      instance
        .post("/Web_KiemTraTemplateID", {
          TemplateID: this.data.templateID,
        })
        .then((response) => {
          if (response.data.data[0].error) {
            this.canCommit = false;
            this.isCommited = false
            this.message = response.data.data[0].message;
            // console.log(response.data.data[0].message)
          } else {
            if (this.data.templateID === "") {
              this.message = "templateID rỗng !";
              this.canCommit = false;
              this.isCommited = false
              return;
            }
            this.canCommit = true;
            instance
              .post("/Web_TemplateUpdate", {
                TemplateID: this.data.templateID,
                TenKhaoSat: this.nameTemplates,
                jsonDataNew: JSON.stringify(this.data),
                NgayBD: moment(this.dates[0]).format("MM-DD-YYYY"),
                NgayKT: moment(this.dates[1]).format("MM-DD-YYYY"),
              })
              .then((response) => {
                this.message = response.data.data[0].message;
                this.isCommited = true
                console.log(response.data);
              })
              .catch((error) => {
                console.log(error);
              });
            // console.log(response.data.data[0].message)
          }
        });
    },
  },
  created(){
    if (this.data.templateID === "") {
        this.message = "templateID rỗng !";
        this.canCommit = false;
    }
  }
};
</script>

<style>
pre {
  overflow-x: auto;
  white-space: pre-wrap;
  white-space: -moz-pre-wrap;
  white-space: -pre-wrap;
  white-space: -o-pre-wrap;
  word-wrap: break-word;
}
</style>