<template>
  <div>
    <!-- <div
        horizontal
        background-color="transparent"
      >
        <draggable
          element="span"
          v-model="controls"
          ghost-class="ghost"
          :sort="false"
          v-bind="dragOptions"
          :group="{ name: 'items', pull: 'clone', put: false }"
          :clone="handleClone"
        >
          <div
            class="d-inline pa-2 grab"
            v-for="(element, i) in controls"
            :key="i"
            light
            @click="scroll();(data.Part[(data.Part.length)-1].Question.push(element)); "
          >
            <span class="blue--text text--darken-4">
              {{ element.Type }}
            </span>
          </div>
        </draggable>
      </div> -->
    <v-card elevation="5">
      <draggable
        element="span"
        v-model="controls"
        :sort="false"
        v-bind="dragOptions"
        :group="{ name: 'items', pull: 'clone', put: false }"
        :clone="handleClone"
      >
        <transition-group
          class="list-group overflow-y-auto rounded"
          three-line
          style="
            max-height: calc(100vh - 100px);
            border-top: 10px solid #0d47a1;
          "
          tag="v-list"
        >
          <v-list-item  
            :ripple="false"
            class="list-group-item grab"
            v-for="(element,i) in controls"
            :key="uuid(element)"
            justify="center"
            @click="
              scroll();
              data.Part[data.Part.length - 1].Question.push(element);
            " 
          >
            <v-list-item-icon :class="(!show)?'mr-0':'mr-5'">
              <v-icon v-text="items[i].icon"></v-icon>
            </v-list-item-icon>
            <v-list-item-title            
              class="is-show"
              v-text="items[i].name"
              aria-hidden="true"
            ></v-list-item-title>
          </v-list-item>
        </transition-group>
      </draggable>
      <!-- <v-btn tile color="blue" outlined block>Thêm</v-btn> -->
    </v-card>
  </div>
</template>

<script>
import draggable from "vuedraggable";
export default {
  name: "",
  props: {
    data: {
      Part: Array,
    },
    scroll: Function,
  },
  data: function () {
    return {
      show: false,
      items: [
        {icon: 'mdi-radiobox-marked', name: 'Trắc nghiệm'},
        {icon: 'mdi-checkbox-marked', name: 'Hộp kiểm'},
        {icon: 'mdi-text-short', name: 'Trả lời ngắn'},
        {icon: 'mdi-text-subject', name: 'Đoạn'},
        {icon: 'mdi-table', name: 'Marktable'}
      ],
      controls: [
        {
          QID: "",
          Type: "Radio",
          Text: "Tiêu đề",
          Option: [
            {
              Text: "Tuỳ chọn 1",
            },
            {
              Text: "Tuỳ chọn 2",
            },
          ],
        },
        {
          GroupID: "",
          Type: "Checkbox",
          Text: "Tiêu đề CheckBox",
          Option: [
            {
              QID: "",
              Text: "Checkbox 1",
            },
          ],
        },
        {
          QID: "",
          Required: false,
          Type: "TextBox",
          Text: "Tiêu đề",
          Holder: "Nhập câu hỏi",
        },
        // {
        //   QID: "",
        //   Type: "FileInput",
        //   Text: "Tiêu đề",
        // },
        {
          QID: "",
          Type: "TextArea",
          Required: false,
          Text: "Tiêu đề",
          Row: "3",
          Holder: "Nhập nội dung",
        },
        {
          Type: "MarkTable",
          Text: "Tiêu đề MarkTable",
          Des: "Mô tả",
          ValueArray: [
            {
              Value: "1",
              Text: "Rất thấp",
            },
            {
              Value: "2",
              Text: "Thấp",
            },
            {
              Value: "3",
              Text: "Trung bình",
            },
            {
              Value: "4",
              Text: "Cao",
            },
            {
              Value: "5",
              Text: "Rất cao",
            },
          ],
          Topic: [
            {
              GroupID: "",
              Name: "Tiêu đề câu hỏi",
              Question: [
                {
                  QID: "",
                  Text: "1. Câu hỏi",
                },
              ],
            },
          ],
        },
      ],
    };
  },
  methods: {
    handleClone(item) {
      // Create a fresh copy of item
      let cloneMe = JSON.parse(JSON.stringify(item));
      this.$delete(cloneMe, "uid");
      return cloneMe;
    },
    uuid(e) {
      if (e.uid) return e.uid;
      const key = Math.random().toString(16).slice(2);
      this.$set(e, "uid", key);
      return e.uid;
    },
  },
  components: {
    draggable,
  },
  computed: {
    dragOptions() {
      return {
        animation: 100,
        disabled: false,
        ghostClass: 'ghost'
      };
    },
  },
};
</script>

<style>
.grab {
  cursor: -webkit-grab;
  cursor: grab;
}
.grab:hover {
  background-color: rgba(114, 114, 114, 0.164);
}
.ghost {
  opacity: 0.8;
  background: rgba(0, 128, 179, 0.274);
}
.is-show {
  display: flex;
  text-align: left;
  margin-left: 5px;
}
@media only screen and (max-width: 1024px) {
  .is-show {
    display: none !important;
  }
}
</style>